<template>
  <transition name="list-fade">
    <div class="playlist" v-show="showFlag" ref="playlist" @click="hide">
      <div class="list-wrapper" @click.stop>
        <div class="list-header">
          <h1 class="title">
            <i class="icon" :class="iconMode" @click="modeChange"></i>
            <span class="text" @click="modeChange">{{getModeText}}</span>
            <span class="clear" @click="alertConfirm">
              <i class="icon-clear"></i>
            </span>
          </h1>
        </div>
        <v-scroll class="list-content" :data="sequenceList" ref="listContent">
          <transition-group name="list" tag="ul">
            <li class="item musicItem" v-for="(item, index) in sequenceList" :key="item.id" @click="selectItem(item)" ref="musicItem">
              <i class="current" :class="getCurrentIcon(item)"></i>
              <span class="text">{{item.name}}</span>
              <span class="like" @click.stop="toggleFavorite(item)">
                <i :class="getFavoriteIcon(item)"></i>
              </span>
              <span class="delete" @click.stop="deleteOne(item)">
                <i class="icon-delete"></i>
              </span>
            </li>
          </transition-group>
        </v-scroll>
        <div class="list-operate">
          <div class="add" @click="addSong">
            <i class="icon-add"></i>
            <span class="text">添加歌曲到队列</span>
          </div>
        </div>
        <div class="list-close" @click="hide">
          <span>关闭</span>
        </div>
      </div>
      <!--弹窗-->
      <v-confirm :text="title" ref="confirm" @confirm="clearList"></v-confirm>
      <!--添加歌曲到列表-->
      <add-song ref="addSong"></add-song>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import Scroll from 'base/scroll/scroll'
  import Confirm from 'base/confirm/confirm'
  import { modeChangeMixin } from 'common/js/mixin'
  import { playMode } from 'common/js/config'
  import AddSong from 'components/add-song/add-song'

  export default {
    mixins: [modeChangeMixin],
    data() {
      return {
        showFlag: false,
        title: '确认清空播放列表吗？'
      }
    },
    computed: {
      ...mapGetters([
        'sequenceList',
        'playList',
        'currentSong'
      ]),
      getModeText() {
        if (this.mode === playMode.sequence) {
          return '顺序播放'
        }
        if (this.mode === playMode.random) {
          return '随机播放'
        }
        return '单曲循环'
      }
    },
    methods: {
      ...mapMutations({
        setCurrentIndex: 'SET_CURRENT_INDEX',
        setPlaying: 'SET_PLAYING'
      }),
      ...mapActions([
        'deleteSongAction',
        'clearSongListAction'
      ]),
      show() {
        this.showFlag = true
        setTimeout(() => {
          this.$refs.listContent.refresh()
          this.scrollToCurrentMusic()
        }, 20)
      },
      hide() {
        this.showFlag = false
      },
      selectItem(item) {
        let resIndex = this.playList.findIndex((song) => {
          return song.id === item.id
        })
        this.setCurrentIndex(resIndex)
        this.setPlaying(true)
      },
      getCurrentIcon(item) {
        if (this.currentSong.id === item.id) {
          return 'icon-play'
        } else {
          return ''
        }
      },
      scrollToCurrentMusic() {
        let resIndex = this.sequenceList.findIndex((song) => {
          return song.id === this.currentSong.id
        })
        let musicItem = document.getElementsByClassName('musicItem')
        this.$refs.listContent.scrollToElement(musicItem[resIndex], 600)
      },
      deleteOne(item) {
        this.deleteSongAction(item)
        if (!this.playList.length) {
          this.hide()
        }
      },
      alertConfirm() {
        this.$refs.confirm.show()
      },
      clearList() {
        this.clearSongListAction()
        this.$refs.confirm.hide()
        this.hide()
      },
      addSong() {
        this.$refs.addSong.show()
      }
    },
    components: {
      'v-scroll': Scroll,
      'v-confirm': Confirm,
      'add-song': AddSong
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .playlist
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 200
    background-color: $color-background-d
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    &.list-fade-enter
    .list-wrapper
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      background-color: $color-highlight-background
      .list-header
        position: relative
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items: center
          .icon
            margin-right: 10px
            font-size: 16px
            color: $color-theme-d
          .text
            flex: 1
            font-size: $font-size-medium
            color: $color-text-l
          .clear
            extend-click()
            .icon-clear
              font-size: $font-size-medium
              color: $color-text-d
      .list-content
        max-height: 300px
        overflow: hidden
        .item
          display: flex
          align-items: center
          height: 40px
          padding: 0 30px 0 20px
          overflow: hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.1s
          &.list-enter, &.list-leave-to
            height: 0
          .current
            flex: 0 0 20px
            width: 20px
            font-size: $font-size-small
            color: $color-theme-d
          .text
            flex: 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-text-d
          .like
            extend-click()
            margin-right: 15px
            font-size: $font-size-small
            color: $color-theme
            .icon-favorite
              color: $color-sub-theme
          .delete
            extend-click()
            font-size: $font-size-small
            color: $color-theme
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display: flex
          align-items: center
          padding: 8px 16px
          border: 1px solid $color-text-l
          border-radius: 100px
          color: $color-text-l
          .icon-add
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-background
        font-size: $font-size-medium-x
        color: $color-text-l
</style>



